.tabs {
  &__navs {}

  &__nav {
    user-select: none;
    cursor: pointer;

    &--active {
      cursor: default;
    }
  }

  &__tabs {}

  &__tab {
    display: none;

    &--active {
      display: block;
    }

    &--transition {
      &-leave {
        opacity: 1;

        &-active {
          transition-duration: 150ms;
        }
        &-to {
          opacity: 0;
        }
      }

      &-enter {
        opacity: 0;

        &-active {
          transition-duration: 150ms;
        }
        &-to {
          opacity: 1;
        }
      }
    }

    &--animation {
      &-leave {
        &-active {
        }
        &-to {
          animation: fadeOut 150ms both;
        }
      }

      &-enter {
        &-active {
        }
        &-to {
          animation: fadeIn 150ms both;
        }
      }
    }
  }
}